.soldier {
    position: relative;
    /* min-height: 100vh; */
    display: flex;
    flex-direction:column;
    margin-top: 50px;
    padding: 0 0 0 62px;
}

.soldier_content{
    height: 70%;
    width: 70%;
    display: flex;
    flex-direction:row;
}
.soldier_image{
    height: 400px;
    width: 300px;
}

.home_level{
    height: 60px;
    width: 60px;
}
.soldier_info{
    margin-top: 10px;
    display: flex;
    flex-direction:row;
}

.soldier_level_info{
    margin: 10px 0 0 20px;
}

.soldier_level_stats ul{
    display: flex;
    padding-right: 40px;
    /* flex-direction:row; */
}

.soldier_level_stats ul li{
    margin-right: 20px;
    font-weight: 500;
    font-size: x-small;
    /* flex-direction:row; */
}
.soldier_level_stats_a{
    padding: 0 10px 0 10px;
    margin-top: 0;
    border: 1px solid #ffffff7e;
}

ul{
    margin-top: 2%;
    padding-left: 0;
    list-style: none;
}

.soldier_select{
    display: flex;
    flex-direction:row;
}

.soldier_name{
    font-weight: 700;
}

@media (max-width: 768px) {
    
    .soldier_content {
        display: flex;
        flex-direction:column;
    }
    .home_level{
        margin-top: 18px;
    }
  }