.soldier_menu{
    width: 200px;
}

.soldier_menu ul li{
    font-weight: 600;
    font-size: small;
    padding: 8px;
    border-top: 1px solid #ffffff7e;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.soldier_menu ul li:hover{
    background-color: rgb(0, 0, 0);     
    /* color: black; */
    filter: invert(100%);
    
}

.stats{
    font-size: x-small;
}


.soldier_game_stats{
    padding-left: 20px;
}

.soldier_stats ul {
    display: flex;
    flex-direction:row;
    padding-top: 5px;
}


.soldier_stats ul li {
    display: flex;
    flex-direction:column;
    font-weight: 500;
    font-size: small;
    padding: 0% 60px 15px 0px;
}

.soldier_top_stats{
    display: flex;
    flex-direction:row;
}

.soldier_stats_value{
    font-weight: 700;
    font-size: x-large;
}
.soldier_top{
    margin: 2px;
    background-color: rgba(0, 0, 0, 0.337);
    width: 150px;
    height: 105px;
    display: flex;
    flex-direction:column;
    padding: 2px 0 2px 5px;
}

.a:hover,.b:hover,.c:hover,.d:hover{
    background-color: black;
    filter: invert(100%);
}

.soldier_top_title{
    font-weight: 600;
    font-size:small;
}

.soldier_top_description, .soldier_top_value{
    font-weight: 600;
    font-size:x-small;
}

.engineer{
    padding-left: 50px;
    width: 100px;
    height: 65px;
}

.info_img{
    /* padding-left: 50px; */
    width: 140px;
    height: 60px;
}

.gun{
    /* padding-left: 50px; */
    width: 140px;
    height: 59px;
}

@media (max-width: 768px) {
    
    .soldier_top_stats{
        flex-direction: column;
    }
    .soldier_stats ul li {
        padding-right: 8px;
    }
  }